<template>
  <section class="login">
    <div class="login-logo">
      <img src="@/assets/images/logo-big.svg" alt="Logo" />
    </div>
    <form class="login-form" @submit.prevent="login">
      <div class="login-username">
        <base-input
          :type="'text'"
          :id="'login'"
          :placeholder="'Логин'"
          :required="true"
          v-model="username"
        />
      </div>
      <div class="login-password">
        <base-input
          :type="'password'"
          :id="'password'"
          :message="'Вы ввели неверный пароль. Проверьте пароль или обратитесь к администратору'"
          :placeholder="'Пороль'"
          :error="passStatus"
          :required="true"
          v-model="password"
        />
      </div>
      <div class="login-signIn">
        <base-button :type="'filled'"> Войти </base-button>
      </div>
      <div class="login-report">
        <base-link
          :to="{ name: 'index' }"
          :size="16"
          :lineHeight="19"
          :color="'#ACACAC'"
          :align="'center'"
        >
          Если забыли пароль, обратитесь к администратору
        </base-link>
      </div>
    </form>
  </section>
</template>

<script>
import BaseInput from "@/components/common/Base.Input.vue";
import BaseButton from "@/components/common/Base.Button.vue";
import BaseLink from "@/components/common/Base.Link.vue";

export default {
  name: "Login",
  components: {
    BaseInput,
    BaseButton,
    BaseLink,
  },
  data() {
    return {
      username: "",
      password: "",
      passStatus: false,
    };
  },
  methods: {
    login() {
      const data = {
        username: this.username,
        password: this.password,
      };
      this.$api.post(`${process.env.VUE_APP_API}/login/`, data).then((res) => {
        const user = res.data.user;
        user.token = res.data.token;
        localStorage.setItem("user", JSON.stringify(user));
        this.$http.defaults.headers.Authorization = `Token ${res.data.token}`;
        let name = this.$route.query.next || "index";
        this.$router.push({ name });
      });
    },
  },
};
</script>

